import Vue from 'vue'
import Vuex from 'vuex'
import api from '@/store/api.js'
import createPersistedState from "vuex-persistedstate";
import router from '../router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    access: '22',
    save: false,
    auth: false,
    user: {},
    nav: true,
    storage: [],
    announcements: [],
    snackbar: {
      show: false,
      message: ''
    }
  },
  getters: {
    getitem: (state) => (id) => {
      console.log(id)
      console.log(state)
    },
    getitems: (state) => (prop) => {
      return state.storage[prop]
    },
    getannouncements: (state) => {
      return state.announcements;
    },
    getsnackbar: (state) => {
      return state.snackbar;
    },
    getuser: (state) => {
      return state.user
    },
    getlastaccess: (state) => {
      return state.access
    }
  },
  mutations: {
    message (state,data){
      state.snackbar.show = true
      state.snackbar.message = data
    },
    login (state,data) {
      state.user=data
      state.auth = true
    },
    logout (state) {
      state.auth = false
    },
    savesession(state){
      state.save = true
    },
    exitsession(state){
      state.save = false
    },
    shownav(state){
      state.nav=true
    },
    hidenav(state){
      state.nav=false
    },
    init(state, data){
      state.storage = data
    },
    loadannouncements(state,data){
      state.announcements = data
    },
    addannouncement(state,data){
      state.announcements.push(data)
    },
    additemstorage(state,data){
      state.storage[data.prop].push(data.value)
    },
    updateitemstorage(state,data){
      state.storage[data.prop].push(data.value)
    },
    lastaccess(state,data){
      state.access = data
    },
    updateuser(state,data){
      state.user = data.value
    }

  },
  actions: {
    async dologin ({ commit },data){

      let response = await api.login(data);
      if(response.data){

        commit('login',response.data)
        let today = new Date().toISOString().substring(0,10)
        commit('lastaccess',today)

        if(response.data.rol == "admin"){
          router.push('/administrador')
        }else{
          router.push('/usuario')
        }
        return true

      }else{
        return false
      }
    },
    async dologout ({ commit }) {
      commit('logout')
    },
    async doinit ({ commit }, data){
      
      if(data.type == 'user'){
        let response = await api.user(data.id);
        commit('init', response.data)
      }
         
    },
    async doloadannouncements ({ commit }){
      
      let response = await api.getannouncements();   
      console.log(response.data)   

      commit('loadannouncements', response.data)
      
    },
    doaddannouncement({commit},data){
      commit('addannouncement',data)
    },
    async doadditemstorage(commit,data){

      let response = await api.createitem(data)
      if(response.data){
        return response.data
      }else{
        return false
      }

    },
    async doupdateitemstorage({commit},data){

      let response = await api.updateitem(data)
      if(response.data){

        if(data.prop == 'personal'){
          commit('updateuser',data)
          
        }
        return true;
        
      }else{
        return false;
      }

    },
    async docreateaccount({commit},data){
      let response = await api.createaccount(data)
      console.log(response.data)
      if(response.data){
        commit('login',response.data)
        return true
      }else{
        return false
      }

    },
    async dorecoverpass (commit,data){
      let response = await api.recoverpass(data);
      if(response.data){
        return true;
      }else{
        return false;
      }
    },
    async update({commit}){

      if(this.state.user.rol == 'administrador'){
        console.log('admin')
      }else{
        let response = await api.user(this.state.user.id);
        commit('init', response.data)
      }

    }
  },
  plugins: [createPersistedState()]
})
