import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/login',
    name: 'loginpersonal',
    component: () => import('../views/personal/login.vue')
  },
  {
    path: '/administrador/login',
    name: 'loginadministrador',
    component: () => import('../views/administrador/login.vue')
  },
  {
    path: '/administrador',
    component: () => import('../views/administrador/home.vue'),
    meta: {
      auth: true
    },
    children: [
      {
        path: '/',
        name: 'administradordashboard',
        component: () => import('../views/administrador/dashboard.vue')
      },
      {
        path: 'usuarios',
        name: 'administradorpersonal',
        component: () => import('../views/administrador/personal.vue')
      },
      {
        path: 'equipo',
        name: 'administradorequipo',
        component: () => import('../views/administrador/equipo.vue')
      },
      {
        path: 'reportes',
        name: 'administradorreportes',
        component: () => import('../views/administrador/reportes.vue')
      }
    ]
  },
  {
    path: '/usuario',
    component: () => import('../views/personal/home.vue'),
    meta: {
      auth: true
    },
    children: [
      {
        path: '/',
        name: 'dashboard',
        component: () => import('../views/personal/dashboard.vue')
      },
      {
        path: 'equipo',
        name: 'equipo',
        component: () => import('../views/personal/equipo.vue')
      },
      {
        path: 'reportes',
        name: 'reportes',
        component: () => import('../views/personal/reportes.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {

 
  if(to.name=='home' || to.name=='loginpersonal' || to.name=='loginadministrador'){
      store.commit('shownav')
  }else{
      store.commit('hidenav')
  }

  if(store.state.save && to.name=="home"){
    next('/usuario')
    store.commit('hidenav')
  }

  if (to.matched.some(record => record.meta.auth)) {
    if (store.state.auth) {

      next()

    } else {
      next('/')
    }
  } else {
    next()
  }

})

export default router
